	module.exports = {"data":{"markdownRemark":{"htmlAst":{"type":"root","children":[{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"This hat is created by myself and my classmate, Tripti Shetty, for our Trends and Forecasting course at LaSalle College. We were instructed to create a hat that looks like a Phillip Treacy hat, who is one of the top couture hat designers in the world. Featuring one of the key Pantone colours of Fall/Winter 2019: PANTONE 18-4048 TCX Nebulas Blue, our hat takes inspiration from the Northern Lights and Nebulae. Nebulas blue is the highlight and primary colour of our hat that is followed by a gradient of light blue and silver to mimick the appearance of the dust and gases seen between the stars. "}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"Model: Emma P"}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"Photographer: Kinjal Naik "}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"Stylists & Designers: Tripti Shetty and Javia Leung "}]}],"data":{"quirksMode":false}},"frontmatter":{"path":"/styling/philip-treacy","title":"Nebulas Blue","origin":"/styling"}},"allFile":{"edges":[{"node":{"name":"NebulasBlue","relativePath":"nebulasblue/NebulasBlue.jpeg"}},{"node":{"name":"NebulasBlue2","relativePath":"nebulasblue/NebulasBlue2.jpeg"}},{"node":{"name":"NebulasBlue3","relativePath":"nebulasblue/NebulasBlue3.jpeg"}},{"node":{"name":"NebulasBlue4","relativePath":"nebulasblue/NebulasBlue4.jpeg"}},{"node":{"name":"NebulasBlueCover","relativePath":"nebulasblue/NebulasBlueCover.jpeg"}}]}},"pathContext":{"imageFolder":"nebulasblue"}}

